/* Variables */

$yldGrey: #181b1e;
$yldGold: #efbc86;

$black: #121212;
$highlightBlue: #3b88fd;
$lightGrey: #ebebeb;

$ptMono: 'IBM Plex Mono', monospace;

/* End Variables */

*, *::before, *::after {
  box-sizing: border-box;
}

.offScreen {
  position: absolute;
  top: -99999px;
  left: -99999px;
  width: 0;
  height: 0;
}

ul.reset {
  padding: 0;
  margin: 0;
  list-style: none;
}

.hide {
  display: none !important;
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;

  /* Hides the slider so custom styles can be added */
  background: transparent;
  border-color: transparent;
  color: transparent;
}

/* Special styling for WebKit/Blink */
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 2.25rem;
  width: 2.25rem;
  border-radius: 50%;
  background: linear-gradient(145deg, rgba(#ddd,1), rgba(#bbb,1));
  cursor: pointer;
  margin-top: -11px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(#fff,0.05), -0.125rem -0.125rem 0.25rem rgba(#000,0.35); /* Add cool effects to your sliders! */
}

/* All the same stuff for Firefox */
input[type=range]::-moz-range-thumb {
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(#fff,0.05), -0.125rem -0.125rem 0.25rem rgba(#000,0.35); /* Add cool effects to your sliders! */
  height: 2.25rem;
  width: 2.25rem;
  border-radius: 50%;
  background: linear-gradient(145deg, rgba(#fff,1), rgba(#ccc,1));
  cursor: pointer;
}

/* All the same stuff for IE */
input[type=range]::-ms-thumb {
  box-shadow: 0.125rem 0.125rem 0.25rem rgba(#fff,0.05), -0.125rem -0.125rem 0.25rem rgba(#000,0.35); /* Add cool effects to your sliders! */
  height: 2.25rem;
  width: 2.25rem;
  border-radius: 50%;
  background: linear-gradient(145deg, rgba(#fff,1), rgba(#ccc,1));
  cursor: pointer;
}

input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: linear-gradient(145deg, rgba($yldGold,0.5), rgba(darken($yldGold,5%),0.5));
  border-radius: 3px;
  border: 0.2px solid #010101;
}

input[type=range]:focus::-webkit-slider-runnable-track {
  background: linear-gradient(145deg, rgba($yldGold,0.5), rgba(darken($yldGold,5%),0.5));
}

input[type=range]::-moz-range-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: linear-gradient(145deg, rgba($yldGold,1), rgba(darken($yldGold,5%),1));
  border-radius: 3px;
  border: 0.2px solid #010101;
}

input[type=range]::-ms-track {
  width: 100%;
  height: 6px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: $yldGold;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]:focus::-ms-fill-lower {
  background: $yldGold;
}
input[type=range]::-ms-fill-upper {
  background: $yldGold;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]:focus::-ms-fill-upper {
  background: $yldGold;
}
